import { useEffect, useState } from "react";
import { supabase } from "../lib/supabaseClient";
import useSupabaseAuth from "../hooks/useSupabaseAuth";
import Login from "../pages/Login";
import "./LoveOrdersAdmin.css";

export default function LoveOrdersAdmin() {
  const { user, profile, loading, signOut } = useSupabaseAuth();
  const [orders, setOrders] = useState([]);
  const [filter, setFilter] = useState("all");
  const [loadingOrders, setLoadingOrders] = useState(false);
  const [message, setMessage] = useState("");
  const [search, setSearch] = useState("");

  //  
  if (loading) return <p style={{ textAlign: "center" }}>...</p>;
  if (!user) return <Login />;
  if (profile?.role !== "admin") {
    return (
      <div style={{ textAlign: "center", marginTop: "100px" }}>
        <h2> </h2>
        <p></p>
        <button
          style={{
            marginTop: "20px",
            background: "#f6d365",
            border: "none",
            padding: "10px 20px",
            borderRadius: "10px",
            cursor: "pointer",
          }}
          onClick={signOut}
        >
          
        </button>
      </div>
    );
  }

  //  
  async function loadOrders() {
    setLoadingOrders(true);
    let query = supabase
      .from("love_orders")
      .select(
        `
        id,
        order_no,
        total_fee,
        status,
        pay_time,
        transaction_id,
        created_at,
        love_access_codes(code)
      `
      )
      .order("created_at", { ascending: false });

    if (filter !== "all") query = query.eq("status", filter);
    const { data, error } = await query;

    if (error) setMessage(" " + error.message);
    else setOrders(data || []);
    setLoadingOrders(false);
  }

  useEffect(() => {
    loadOrders();
  }, [filter]);

  //  
  const filtered = orders.filter((o) =>
    o.order_no.toLowerCase().includes(search.toLowerCase())
  );

  //   CSV
  const exportCSV = () => {
    const header = ["", "()", "", "", "", ""];
    const rows = filtered.map((o) => [
      o.order_no,
      (o.total_fee / 100).toFixed(2),
      o.status,
      o.transaction_id || "-",
      o.love_access_codes?.code || "-",
      o.pay_time ? new Date(o.pay_time).toLocaleString() : "-",
    ]);
    const csv = [header, ...rows].map((r) => r.join(",")).join("\n");
    const blob = new Blob([csv], { type: "text/csv" });
    const link = document.createElement("a");
    link.href = URL.createObjectURL(blob);
    link.download = `love_orders_${new Date().toISOString().split("T")[0]}.csv`;
    link.click();
  };

  //  
  return (
    <div className="love-orders-admin">
      <div className="admin-header">
        <h2> </h2>
        <div className="admin-info">
          <span>{profile?.full_name || profile?.email}</span>
          <button onClick={signOut} className="logout-btn"></button>
        </div>
      </div>

      <div className="top-bar">
        <div className="filters">
          <select value={filter} onChange={(e) => setFilter(e.target.value)}>
            <option value="all"></option>
            <option value="pending"></option>
            <option value="paid"></option>
            <option value="failed"></option>
          </select>
          <input
            type="text"
            placeholder=" "
            value={search}
            onChange={(e) => setSearch(e.target.value)}
          />
        </div>

        <button className="export-btn" onClick={exportCSV}>
            CSV
        </button>
      </div>

      {message && <p className="message">{message}</p>}
      {loadingOrders ? (
        <p>...</p>
      ) : (
        <table className="orders-table">
          <thead>
            <tr>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
            </tr>
          </thead>
          <tbody>
            {filtered.map((o) => (
              <tr key={o.id} className={o.status}>
                <td>{o.order_no}</td>
                <td>{(o.total_fee / 100).toFixed(2)}</td>
                <td>
                  {o.status === "paid"
                    ? " "
                    : o.status === "pending"
                    ? " "
                    : " "}
                </td>
                <td>{o.transaction_id || "-"}</td>
                <td>{o.love_access_codes?.code || "-"}</td>
                <td>{o.pay_time ? new Date(o.pay_time).toLocaleString() : "-"}</td>
                <td>{new Date(o.created_at).toLocaleString()}</td>
              </tr>
            ))}
          </tbody>
        </table>
      )}
    </div>
  );
}
