import { useEffect, useState } from "react";
import { supabase } from "../lib/supabaseClient";
import "./LoveCodeAdmin.css";

export default function LoveCodeAdmin() {
  const [codes, setCodes] = useState([]);
  const [filter, setFilter] = useState("all");
  const [loading, setLoading] = useState(false);
  const [newCount, setNewCount] = useState(10);
  const [message, setMessage] = useState("");

  async function loadCodes() {
    try {
      setLoading(true);
      let query = supabase
        .from("love_access_codes")
        .select("*")
        .order("created_at", { ascending: false });

      if (filter === "unused") query = query.eq("is_used", false);
      if (filter === "used") query = query.eq("is_used", true);

      const { data, error } = await query;

      if (error) {
        console.error(" Supabase :", error.message);
        setMessage(" " + error.message);
        setCodes([]);
      } else {
        setCodes(data || []);
      }
    } catch (err) {
      console.error(" :", err);
      setMessage(" " + err.message);
    } finally {
      setLoading(false);
    }
  }

  useEffect(() => {
    loadCodes();
  }, [filter]);

  async function generateCodes() {
    try {
      setLoading(true);
      const { data, error } = await supabase.functions.invoke("generate-love-codes", {
        body: JSON.stringify({ count: newCount }),
      });

      if (error) {
        console.error(" :", error.message);
        setMessage(" " + error.message);
      } else {
        setMessage(`  ${newCount} `);
        await loadCodes();
      }
    } catch (err) {
      console.error("  Edge Function :", err);
      setMessage(" " + err.message);
    } finally {
      setLoading(false);
    }
  }

  function exportCSV() {
    const csv = [
      ["code", "price", "discount_price", "is_used", "used_at"].join(","),
      ...codes.map((c) =>
        [c.code, c.price, c.discount_price, c.is_used, c.used_at || ""].join(",")
      ),
    ].join("\n");

    const blob = new Blob([csv], { type: "text/csv" });
    const link = document.createElement("a");
    link.href = URL.createObjectURL(blob);
    link.download = "love_access_codes.csv";
    link.click();
  }

  return (
    <div className="love-admin">
      <h2> </h2>

      <div className="actions">
        <select value={filter} onChange={(e) => setFilter(e.target.value)}>
          <option value="all"></option>
          <option value="unused"></option>
          <option value="used"></option>
        </select>

        <input
          type="number"
          min="1"
          max="100"
          value={newCount}
          onChange={(e) => setNewCount(Number(e.target.value))}
        />
        <button onClick={generateCodes} disabled={loading}>
           
        </button>
        <button onClick={exportCSV}>  CSV</button>
      </div>

      {message && <p className="message">{message}</p>}
      {loading && <p>...</p>}

      {!loading && codes.length > 0 && (
        <table className="code-table">
          <thead>
            <tr>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
            </tr>
          </thead>
          <tbody>
            {codes.map((c) => (
              <tr key={c.code} className={c.is_used ? "used" : "unused"}>
                <td>{c.code}</td>
                <td>{c.price}</td>
                <td>{c.discount_price}</td>
                <td>{c.is_used ? " " : " "}</td>
                <td>{c.used_at ? new Date(c.used_at).toLocaleString() : "-"}</td>
              </tr>
            ))}
          </tbody>
        </table>
      )}
    </div>
  );
}
