import React, { useState } from "react";
import "./SiteHeader.css";

/**
 *  / + 
 */
export default function SiteHeader({ onNavigate }) {
  const [menuOpen, setMenuOpen] = useState(false);
  const [user, setUser] = useState(null); // 
  const [showAuthModal, setShowAuthModal] = useState(false);
  const [isLoginMode, setIsLoginMode] = useState(true);

  const handleLogin = (e) => {
    e.preventDefault();
    setUser({ name: "" });
    setShowAuthModal(false);
  };

  const handleLogout = () => {
    setUser(null);
  };

  return (
    <header className="site-header">
      <div className="header-container">
        {/*  Logo +  */}
        <div className="logo" onClick={() => onNavigate?.("home")}>
           
        </div>

        {/*   */}
        <nav className={`nav-links ${menuOpen ? "open" : ""}`}>
          <button onClick={() => onNavigate?.("home")}></button>
          <button onClick={() => onNavigate?.("tests")}></button>
          <button onClick={() => onNavigate?.("admin")}></button>
          <button onClick={() => onNavigate?.("orders")}></button>
        </nav>

        {/*   */}
        <div className="auth-section">
          {user ? (
            <>
              <span className="welcome">{user.name}</span>
              <button className="logout-btn" onClick={handleLogout}>
                
              </button>
            </>
          ) : (
            <button
              className="login-btn"
              onClick={() => {
                setIsLoginMode(true);
                setShowAuthModal(true);
              }}
            >
               / 
            </button>
          )}
        </div>

        {/*   */}
        <button
          className={`menu-toggle ${menuOpen ? "active" : ""}`}
          onClick={() => setMenuOpen(!menuOpen)}
        >
          
        </button>
      </div>

      {/*   */}
      {showAuthModal && (
        <div className="auth-modal">
          <div className="auth-box">
            <h3>{isLoginMode ? "" : ""}</h3>
            <form onSubmit={handleLogin}>
              <input type="email" placeholder="" required />
              <input type="password" placeholder="" required />
              <button type="submit" className="btn-primary">
                {isLoginMode ? "" : ""}
              </button>
            </form>
            <p>
              {isLoginMode ? "" : ""}{" "}
              <span
                className="switch-mode"
                onClick={() => setIsLoginMode(!isLoginMode)}
              >
                {isLoginMode ? "" : ""}
              </span>
            </p>
            <button className="close-modal" onClick={() => setShowAuthModal(false)}>
              
            </button>
          </div>
        </div>
      )}
    </header>
  );
}
